import React from 'react';
import AbstractList from '../Core/AbstractList'


class GoodsRawMaterial extends AbstractList {

	constructor( props ) {
		super( props );
		this.getCreatePanel = this.getCreatePanel.bind( this );
	}

	getEntityName() {
		return "goodsRawMaterial";
	}

	getSearchPanel() {
		return [
			{ id: "EQ:title", type: 'text', label: 'عنوان' }
		]
	}

	getTableHead() {
		return ["شناسه", "عنوان" , "نوع", "ضریب تاثیر", "کلا", ""];
	}

	getCreatePanel() {
		return [
			{ id: "title", type: 'text', label: 'عنوان' },
			{ id: "importFactor", type: 'text', label: 'ضریب تاثیر' },
			this.createSuggest( "category", 'نوع', "نوع" , "rawMaterialCategory" ),
			this.createSuggest( "material", 'ماده اولیه', "ماده اولیه" , "rawMaterial" ),
			this.createSuggest( "goods", 'کالا', "کالا" , "goods" ),
			
		];
	}

	saveEntity(entity) {
		this.refineSuggestData("category", "rawMaterialCategory", entity);
		this.refineSuggestData("goods", "goods", entity);
		this.refineSuggestData("material", "rawMaterial", entity);
		super.saveEntity( entity );
	}

	renderTitle( entity ) {
		return ( entity ? entity.title : '');
	}

	convertToTableRow( data ) {
		return (
			<tr key={"item_" + data.id}>
				<td>{data.id}</td>
				<td>{data.title}</td>
				<td>{data.selectAble ? this.renderTitle( data.category ) : this.renderTitle( data.material ) }</td>
				<td>{data.importFactor}</td>
				<td>{data.goods.title}</td>
				<td>
					<i className="btn fa fa-pencil" aria-hidden="true" onClick={this.editEntity.bind(this, data.id)}></i>
				</td>
			</tr>
		);
	}

}


export default GoodsRawMaterial;


